<template lang="pug">
	.flex-cont(v-bind:class="{flex: transform, 'opacity-show': auth}", ref="get-width")
		.main-layer(v-bind:class="{transform: transform}", ref="transform")
			LeftSidebar(
				v-if="showLeft"
				v-model="activeCampaign"
			)

			SettingsSidebar(
				v-if="showLeftSettings"
			)

			AddCampaign(
				v-bind:class="{active: addCampaign}"
				v-if="showLeft",
				v-click-outside="handleFocusOut"
			)

			IntegratePopup(
				v-if="showIntegrate"
			)

			LeadGoal(
				v-if="editGoal"
			)

			router-view

			RightSidebar(
				:activeCampaign="activeCampaign"
				v-if="showRight"
			)

</template>

<style lang="scss">
	@import "./mainStyles/main.scss";
</style>

<script>
	import LeftSidebar from '@/components/Left-Sidebar'
	import RightSidebar from '@/components/Right-Sidebar'
	import AddCampaign from '@/components/Add-Campaign'
	import IntegratePopup from '@/components/Integrate-Popup'
	import LeadGoal from '@/components/Lead-Goal'
	import SettingsSidebar from '@/components/Settings-Sidebar'
	export default {
		name: 'App',
		components: {
			LeftSidebar,
			RightSidebar,
			AddCampaign,
			IntegratePopup,
			LeadGoal,
			SettingsSidebar
		},
		data(){
			return{
				activeCampaign: null,
				showLeft: false,
				showLeftSettings: false,
				showRight: false,
				addCampaign: false,
				addCampaignBTN: false,
				showIntegrate: false,
				editGoal: false,
				transform: false,
				auth: true
			}
		},
		computed: {
			AddCampaign() {
				return this.$store.getters.AddCampaign
			},
			SC(){
				return this.$store.getters.SC.load;
			}
		},
		created: function(){
			this.checkRouter();
			if (this.$store.getters.SC.load) {
				this.auth = true;
			}
			window.addEventListener("resize", this.handleResize);
		},
		watch: {
			AddCampaign(){
				this.NewCampaign();
			},
			SC(){
				this.auth = true;
			},
			$route() {
				this.checkRouter();
			}
		},
		methods:{
			NewCampaign(){
				var st = this.$store.state.AddCampaign;
				var status = st.status === 'true';
				if(status){
					this.addCampaign = true;
					this.addCampaignBTN = true;
					setTimeout(() => {
						this.addCampaignBTN = false;
					}, 250)
				}else{
					this.addCampaign = false;
				}
			},
			checkRouter(){
				setTimeout(() => {
					this.checkResize();
				}, 20);
				if (this.$route.path == '/') {
					this.showRight = true;
					this.showLeft = true;
					this.showLeftSettings = false;
					this.transform = true;
				}else if (this.$route.path == '/add-new-campaign') {
					this.showLeft = true;
					this.showRight = false;
					this.showLeftSettings = false;
				}else if(this.$route.name == 'Settings'){
					this.showRight = false;
					this.showLeft = false;
					this.showLeftSettings = true;
					this.transform = true;
				}else{
					this.showRight = false;
					this.showLeft = false;
					this.auth = true;
					this.showLeftSettings = false;
				}
			},
			checkResize(){
				var originalWidth = this.$refs.['get-width'].clientWidth;
				if (this.$route.path != '/mobile') {
					if (originalWidth < 1200) {
						this.transform = false;
						this.showRight = false;
						this.showLeft = false;
						this.auth = true;
						this.showLeftSettings = false;
						this.$router.push('/mobile');
					}
				}

				if (this.$route.path == '/mobile') {
					if (originalWidth >= 1200) {
						this.$router.push('/');
					}
				}
			},
			handleResize() {
				this.checkResize();
			},
			handleFocusOut() {
				if (!this.addCampaignBTN) {
					window.AddC.deleteData();
					this.addCampaign = false;
				}
			}
		}
	}
</script>
