</template>

<script>
	import $ from 'jquery'
	export default {
		name: 'Map',
		props: {
			geo: [Object, Array],
			map: [Object, Array]
		},
		data() {
			return {
				geoData: this.geo,
				mapPos: this.map
			}
		},
		created(){

		},
		mounted () {
			var legends = this.geoData;
			var colorsList = ["#004062", "#0A547D", "#0E689B", "#1E83BC", "#2BA4E8", "#09B3EB", "#02B8F3", "#1CC7FF", "#55D5FF", "#70DCFF"];
			var down = false;
			var offsetX = 0;
			var offsetY = 0;
			var dragBox = $('.drag');
			var dragCont = $('.map-box');
			var x = 0;
			var xBefore = 0;
			var y = 0;
			var yBefore = 0;
			var zoom = parseInt(dragBox.css('width'))  * 0.2;
			// var dragWidth = 0;
			// var dragHeight = 0;

			var start = 0;

			function fillMap(){
				for (var item in legends) {
					$('#'+legends[item].geolocation.code).find('path').css({'fill': colorsList[start], 'stroke': colorsList[start]});
					$('#'+legends[item].geolocation.code).addClass('fill')

					start = +1;
					if (start > colorsList.length) {
						start = 0;
					}
				}
			}

			if (this.mapPos.id == 1) {
				dragBox.css('width', '3000px')
				dragBox.css('left', '-1200px')
				dragBox.css('top', '-620px')
			}else if (this.mapPos.id == 2) {
				dragBox.css('width', '2400px')
				dragBox.css('left', '-200px')
				dragBox.css('top', '-600px')
			}else{
				dragBox.css('width', parseInt(dragCont.css('width')) + 'px')
				dragBox.css('left', '0px')
				dragBox.css('top', '0px')
			}

			$(document).on('click', '.zoom-in', function(){
				var width = parseInt(dragBox.css('width'))  + zoom;
				dragBox.css('width', width +'px');
				// dragWidth = parseInt(dragBox.css('width'));
			});

			$(document).on('click', '.zoom-out', function(){
				var width = parseInt(dragBox.css('width'))  - zoom;
				if (width < parseInt(dragCont.css('width'))) {
					dragBox.css('width', parseInt(dragCont.css('width')) +'px');
				}else{
					dragBox.css('width', width +'px');
				}
				// dragWidth = parseInt(dragBox.css('width'));
			});

			$(dragBox).mousedown(function(evt) {
				down = true;
				offsetX = parseInt(dragBox.css('left'));
				offsetY = parseInt(dragBox.css('top'));
				xBefore = evt.pageX - dragCont.offset().left;
				yBefore = evt.pageY - dragCont.offset().top;
				$('.overlay').fadeIn(0);
				// dragWidth = parseInt(dragBox.css('width'));
				// dragHeight = parseInt(dragBox.css('height'));
			});

			$('.map-box').mousemove(function(evt){
				if(down){
					x = evt.pageX - $('.map-box').offset().left - xBefore + offsetX;
					y = evt.pageY - $('.map-box').offset().top - yBefore + offsetY;
					// var horizontalLimit = parseInt(dragCont.css('width')) - dragWidth;
					// var verticalLimit = parseInt(dragCont.css('height')) - dragHeight;
					// if(x > horizontalLimit && x < 0 || x < 0 && x > Math.abs(horizontalLimit)){
					// 	$(dragBox).css("left", x +'px')
					// }
					// if(y > verticalLimit && y < 0 || y < 0 && y > Math.abs(verticalLimit)){
					// 	$(dragBox).css("top", y +'px')
					// }
					$(dragBox).css("left", x +'px')
					$(dragBox).css("top", y +'px')
				}
			});

			$(document).mouseup(function() {
				down = false;
				offsetX = 0;
				offsetY = 0;
				x = 0;
				xBefore = 0;
				y = 0;
				yBefore = 0;
				$('.overlay').fadeOut(0);
			});

			$('.map g').hover(function() {
				if ($(this).hasClass('fill')) {
					$(".map-tooltip").addClass('active');
					$(".map-tooltip").find('.orange').text($(this).attr('id'));
					var geo = legends.find(item => item.geolocation.code === $(this).attr('id'));
					$(".map-tooltip").find('.orange').text($(this).attr('id'));
					// $(".map-tooltip").find('.orange').text(geo.name);
					$(".map-tooltip").find('.gray').text(geo.leads_geolocation);
					$(".map-tooltip").find('.blue').css('color', $(this).find('path').css('fill'));
					if (geo.percent > 0) {
						$(".map-tooltip").find('.blue').text('('+ geo.percent + '%)');
					}else{
						$(".map-tooltip").find('.blue').hide();
					}
				}
			}, function() {
				$(".map-tooltip").removeClass('active');
				$(".map-tooltip").find('.orange').text();
				$(".map-tooltip").find('.gray').text();
				$(".map-tooltip").find('.blue').show();
			});

			$(dragBox).on('mousemove', function(e){
				var width = Number(parseInt($(".map-tooltip").css('width'))).toFixed(0);
				var offsetX = Number(parseInt($(dragBox).css('left'))).toFixed(0);
				var offsetY = Number(parseInt($(dragBox).css('top'))).toFixed(0);
				// var offsetX = 0;
				// var offsetY = 0;
				var left;
				if (offsetX < 0) {
					left = e.pageX - $('.map-box').offset().left - width / 2 + Math.abs(offsetX);
				}else{
					left = e.pageX - $('.map-box').offset().left - width / 2 - Math.abs(offsetX);
				}

				var top;
				if (offsetY < 0) {
					top = e.pageY - $('.map-box').offset().top + 10 + Math.abs(offsetY)
				}else{
					top = e.pageY - $('.map-box').offset().top + 10 - Math.abs(offsetY)
				}

				$(".map-tooltip").css({
					left: left,
					top: top
				});
			});

			fillMap();
		},
		methods: {
			
		}
	}
</script>