						rect(x="96" y="112" width="15" height="15" rx="3" fill="#86F000")
						rect(x="80" y="112" width="15" height="15" rx="3" fill="#86F000")
						rect(x="64" y="112" width="15" height="15" rx="3" fill="#86F000")
